
<template>
    <div> 
        <br><br><br><br><br>
        <h2>***  CHAT ***</h2>
        <ul id="messages"></ul>
        <form action="">
            <div>
                formmmmmm
            </div>
            <input id="m" autocomplete="off" />
            <button>Send</button>
        </form>
    </div>
</template>


<!--
//src="https://code.jquery.com/jquery-1.11.1.js"
-->


    
<script>
import io from 'socket.io-client';  //problem with uws module
console.log(' ** io **', io);

export default {
  name: 'chatSocket',

  data() {
    return {};
  },

  created() {
  },

  mounted() {
     var nickname = prompt('Your name?');
        var socket = io();
        socket.emit('chat join', nickname);

        socket.on('chat connected', function (nickname) {
            $('#messages').append($('<li>').text(nickname + ' Joined'));
        });

        socket.on('chat message', function (msg) {
            $('#messages').append($('<li>').text(msg));
        });

        $('form').submit(function () {
            socket.emit('chat send-message', nickname + ':' + $('#m').val());
            $('#m').val('');
            return false;
        });
  },

  methods: {
    f1() {}
  },

  computed: {
    f2() {}
  },

  components: {}
};
</script>




